.rating {
    .checked {
        color: orange;
        transform: scale(1);
        transition: transform 0.1s ease-in-out,opacity 0.5s ease-in-out;
    }
    
    .checked:hover {
        transform: scale(1.2);
        transition: transform 0.1s ease-in-out,opacity 0.5s ease-in-out;
    }
}

