@mixin media() {
    @media (max-width: 768px) {
        @content;
    }
}

$font-stack: 'Inconsolata', monospace;
$primary-color: rgb(0, 0, 0);

* {
    margin: 0;
    padding: 0;
    
    .main {
        background-color: #FFF;
        height: 100%;
        padding-bottom: 10px;

        .header {
            text-align: center;
            font-family: 'Benne', serif;
            font-size: large;
            color: black;
        }

        .search {
            font: 100% $font-stack;
            display: flex;
            flex-direction: column;
            position: relative;
            align-content: center;
            justify-content: center;
            z-index: 5;
            margin-top: 2%;
            margin-bottom: 5%;
        
            .search-inner {
                display: flex;
        
                .search-text-box-results {
                    color: $primary-color;
                    font-size: large;
            
                    border: 10px solid white;
                    border-bottom: 1px solid lightgray;
                    height: 40px;
                    width: 90vw;
            
                    margin: 0px 5% 0px;
                    padding: 2px 30px 2px 0px;
                    outline: 0;
                    background-color: white;
                    box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, .5);
                }
        
                .search-text-box-no-results {
                    color: $primary-color;
                    font-size: large;
            
                    border: 10px solid white;
                    height: 40px;
                    width: 90vw;
            
                    margin: 0px 5% 0px;
                    padding: 2px 30px 2px 0px;
                    outline: 0;
                    background-color: white;
                    box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, .5);
                }
            }
        }
        
        .bookshelf-container {
            display: flex;
            overflow-y: auto;
            padding-top: 2%;

            .bookshelf {
                display: grid;
                grid-template-columns: repeat(6, 1fr);
                grid-template-rows: auto;
                grid-gap: 3%;
                grid-row-gap: 10%;
                width: 95%;
                padding: 2%;

                @include media {
                    grid-template-columns: repeat(3, 1fr);
                    padding-top: 10%;
                    grid-row-gap: 5%;
                }
        
                .book {
                    position: relative;
                    z-index: 1;
                    width: 90%;
                    height: 5%;
                    padding-bottom: 150%;
                    perspective: 1000px;
                    cursor: pointer;
                    
                    .book-cover {
                        position: absolute;
                        z-index: 1;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        overflow: hidden;
                        border: solid 5px black;
                        
                        background-size: cover;
                        perspective-origin: 50% 50%;
                        transform-style: preserve-3d;
                        transform-origin: top center;
                        will-change: transform;
                        transform: skewX(0.01deg);
                        transition: transform 0.35s ease-in-out;
                    }

                    .book-cover:after {
                        transform: translatey(-20%);
                        will-change: transform;
                        transition: transform 0.65s cubic-bezier(0.18, 0.9, 0.58, 1);
                    }
                }

                .book:hover .book-cover {
                    transform: rotateX(7deg) translateY(-6px);
                }

                .book:hover .book-cover:after {
                    transform: translateY(0%);
                }

                .book:hover:before {
                    opacity: 0.6;
                    transform: rotatex(7deg) translatey(-6px) scale(1.05);
                }

                .book:before {
                    display: block;
                    content: "";
                    position: absolute;
                    top: 5%;
                    left: 5%;
                    width: 90%;
                    height: 90%;
                    background: rgba(0, 0, 0, 0.1);
                    box-shadow: 0 6px 12px 12px rgba(0, 0, 0, 0.3);
                    will-change: skewx(0.001deg);
                    transition: transform 0.35s ease-in-out,opacity 0.5s ease-in-out;
                }
            }
        }
    }
}